/* Overlays */

.ui-widget-overlay {
    background: #aaaaaa url("images/ui-bg_flat_0_aaaaaa_40x100.png") 50% 50% repeat-x;
    opacity: .30;
    filter:Alpha(Opacity=30);
}

.ui-widget-shadow {
    margin: -8px 0 0 -8px;
    padding: 8px;
    background: #aaaaaa url("images/ui-bg_flat_0_aaaaaa_40x100.png") 50% 50% repeat-x;
    opacity: .30;
    filter:Alpha(Opacity=30);
    -webkit-border-radius: 8px; /* Safari 4 */
    -moz-border-radius: 8px; /* Firefox 3.6 */
    border-radius: 8px;
}

/* IE/Win - Fix animation bug - #4615 */
